.product__section {
    width: auto;
    height: calc(100% - 48px);
    position: relative;
    background-color: red;
    overflow: hidden;
    left: 50%;
    transform: translateX(-50%);
}

.product-background {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

body {
    height: 100%;
}

html {
    height: 100%;
}